.side-bar{
    margin-top: 1rem;
    width: 80%;
    height: 1022px;
    background: var(--white-bg);
    position: sticky;
    top: 8%;
    padding-left: 2%;
    padding-top: 40px;
    
}

.shortcut-links img{
    width: 15px;
    margin-right: 20px;
}

.side-link{
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    width: 80%;
    flex-wrap: wrap;
    cursor: pointer;
    background: var(--white-bg);
    color: var(--dark-font);
    padding: 10px;
    border-radius: 5%;
    font-size: 0.75rem;
}
.side-link .first{
    filter: invert(100%);
}
.active .inv{
    filter: invert(100%);
}
.active{
    background-color: #fcbf49;
    color: #fff;
}

.log{
    border: 1.5px solid #fcbf49;
}